import './customers.css'
import Header from '../../components/Header'
import Title from '../../components/Title'
import { FiUser } from "react-icons/fi";
import { useState } from 'react';
import { db, auth } from '../../services/firebase';
import { toast } from 'react-toastify';
import { addDoc, collection } from 'firebase/firestore';
export default function Customers(){

    const [nomeFantasia, setNomeFantasia] = useState('')
    const [cnpj, setCnpj] = useState('')
    const [endereco, setEndereco] = useState('')

    async function handleAdd(e){
        e.preventDefault()
        if (nomeFantasia != '' && cnpj != '' && endereco != ''){

        const colRef = await addDoc(collection(db, 'clientes'),{
            NomeFantasia: nomeFantasia,
            Cnpj: cnpj,
            Endereco: endereco
        }).then(()=>{
            setNomeFantasia('')
            setCnpj('')
            setEndereco('')
            toast.info('Empresa cadastrada com sucesso!')
        }).catch((error)=>{
            console.log(error)
            toast.error('Erro ao cadastrar a empresa')
        })

        }else{
            toast.warn('Preencha todos os campos!!')
        }
    }

    return(
        <div>

            <Header/>

            <div className='content' >

            <Title name='Clientes'>
                <FiUser size={25}/>
            </Title>

            <div className='container' >

                <form className='form-profile customers' onSubmit={handleAdd}>

                    <label>Nome Fantasia:</label>
                    <input type='text' placeholder='Digite o nome da empresa' value={nomeFantasia} onChange={(event) => setNomeFantasia(event.target.value)} />

                    <label>CNPJ:</label>
                    <input type='text' placeholder='Digite o CNPJ da empresa' value={cnpj} onChange={(event) => setCnpj(event.target.value)}/>

                    <label>Endereo:</label>
                    <input type='text' placeholder='Digite o endereo da empresa' value={endereco} onChange={(event) => setEndereco(event.target.value)}/>

                    <button type='submit'>Cadastrar</button>

                </form>

            </div>

            </div>
            
        </div>
    )
}