import { useContext,useState } from 'react'
import Header from '../../components/Header'
import './profile.css'
import Title from '../../components/Title'
import { FiSettings,FiUpload } from 'react-icons/fi'
import { AuthContext } from '../../contexts/user'
import avatar from '../../assets/avatar.png'
import { db,auth,storage } from '../../services/firebase'
import { doc, updateDoc } from 'firebase/firestore'
import { toast } from 'react-toastify'
import { getDownloadURL, ref, uploadBytes } from 'firebase/storage'
import { async } from '@firebase/util'



export default function Profile(){

    const {user,signOut, storageUser, setUser} = useContext(AuthContext)
    const [nome,setNome] = useState(user && user.Nome)
    const [sobrenome,setSobrenome] = useState(user && user.Sobrenome)
    const [email,setEmail] = useState(user && user.Email)
    const [avatarUrl,setAvatarUrl] = useState(user && user.AvatarUrl)
    const [imageAvatar, setImageAvatar] = useState(null)



    function handleFile(e){
       
            if(e.target.files[0]){
                const image = e.target.files[0]
                if(image.type == 'image/jpeg' || image.type == 'image/png'){
                    setImageAvatar(image)
                    setAvatarUrl(URL.createObjectURL(image))
                    console.log(avatarUrl)
                    
                }else{
                    toast.error('Este formato de arquivo no  permitido! Tente novamente utilizando arquivos .jpg ou .png')
                    setImageAvatar(null)
                    return null;
                }
            }
        
        
    }


    async function handleUpload(){
        
        const currentUid = user.uid
        const storageRef = ref(storage,`images/${currentUid}/${imageAvatar.name}`)
        await uploadBytes(storageRef, imageAvatar).then(async (snapshot)=>{
            console.log('Imagem enviada com sucesso!')
        })
        getDownloadURL(ref(storage, storageRef)).then(async (url)=>{
            let urlFoto = url
            const docRef = doc(db, 'usuario', user.uid);
            await updateDoc(docRef, {
                AvatarUrl: url,
                Nome: nome
            }).then(()=>{
                let data = {
                    ...user,
                    Nome: nome,
                    AvatarUrl: urlFoto
                }
                setUser(data);
                storageUser(data);
            })
        })
        
    }


    async function handleSave(event){
        event.preventDefault();
        
        if(imageAvatar == null && nome != '' && sobrenome != ''){
            const docRef = doc(db, 'usuario', user.uid);
             await updateDoc(docRef, {
                Nome: nome,
                Sobrenome: sobrenome
            })
            .then(()=>{
                let data = {
                    ...user,
                    Nome: nome,
                    Sobrenome: sobrenome
                }
                setUser(data);
                storageUser(data);
            })
            
        }
        else if (nome != '' && sobrenome != '' && imageAvatar != null){
            handleUpload();
        }
    }

    return(
    <div>

        <Header/>

        <div className='content' >
        <Title name='Meu perfil' >
            <FiSettings size={25} />
        </Title>

        <div className='container' >
            <form className='form-profile' onSubmit={handleSave}>
                <label className='label-avatar' >
                    <span>
                        <FiUpload color='#fff' size={25} />
                    </span>
                    <input type='file' accept='image/*' onChange={handleFile} /><br/>
                    {avatarUrl == null ? 
                        <img src={avatar} width='250' height='250' alt='foto de perfil' />
                        :
                        <img src={avatarUrl} width='250' height='250' alt='foto de perfil' />
                    }
                </label>
                <label>Nome</label>
                <input type='text' value={nome} onChange={(event) => setNome(event.target.value)}/>
                <label>Sobrenome</label>
                <input type='text' value={sobrenome} onChange={(event) => setSobrenome(event.target.value)}/>
                <label>E-mail</label>
                <input type='text' value={email} disabled={true}/>
                <button type='submit' >Salvar Alteraes</button>
            </form>
        </div>
        <div className='container' >
            <button className='logout-btn' onClick={()=> signOut()} >Sair</button>
        </div>


        </div>

    </div>
)}