import { useState, useEffect} from "react"
import { db } from "../../services/firebase"
import Title from '../../components/Title'
import Header from "../../components/Header"
import Modal from "../../components/Modal"
import {FiMessageSquare, FiPlus, FiSearch, FiEdit2} from 'react-icons/fi'
import { Link } from "react-router-dom"
import './dashboard.css'
import { doc,collection,query,orderBy, limit, getDoc, DocumentSnapshot, onSnapshot, startAfter } from "firebase/firestore"
import {format} from 'date-fns';


export default function Dashboard(){

    


    const [chamados, setChamados] = useState([])
    const [loading, setLoading] = useState(true)
    const [loadingMore, setLoadingMore] = useState(false)
    const [isEmpty, setIsEmpty] = useState(false)
    const [lastDocs, setLastDocs] = useState()
    const [showPostModal, setShowPostModal] = useState(false)
    const [detail,setDetail] = useState()




    useEffect(()=>{
        loadChamados();
        
        return() => {}

    },[])

    async function loadChamados(){
        const colRef = collection(db, 'chamados')
        const q = query(colRef, orderBy("Created", "desc"), limit(5))
        onSnapshot(q, (col) => {
            const isCollectionEmpty = col.size === 0
            if(!isCollectionEmpty){
            let minhaLista = []
            col.docs.forEach((item)=>{
                minhaLista.push({
                    id: item.id,
                    assunto: item.data().Assunto,
                    cliente: item.data().Cliente,
                    clienteID: item.data().ClienteID,
                    created: item.data().Created,
                    createdFormated: format(item.data().Created.toDate(), 'dd/MM/yyyy'),
                    status: item.data().Status,
                    complemento: item.data().Complemento 
                })
            })
            
            const lastDoc = col.docs[col.docs.length -1]

            setChamados(chamados => [...chamados, ...minhaLista])
            setLastDocs(lastDoc)

        }else{
            setIsEmpty(true)
        }

        setLoadingMore(false)
        setLoading(false)
        })
        
    }

    function togglePostModal(item){
        setShowPostModal(!showPostModal)
        setDetail(item)
    }


    async function handleMore(){
        setLoadingMore(true)
        const colRef = collection(db, 'chamados')
        const q = query(colRef, orderBy("Created", "desc"), startAfter(lastDocs), limit(5))
        onSnapshot(q, (col) => {
            const isCollectionEmpty = col.size === 0
            if(!isCollectionEmpty){
            let minhaLista = []
            col.docs.forEach((item)=>{
                minhaLista.push({
                    id: item.id,
                    assunto: item.data().Assunto,
                    cliente: item.data().Cliente,
                    clienteID: item.data().ClienteID,
                    created: item.data().Created,
                    createdFormated: format(item.data().Created.toDate(), 'dd/MM/yyyy'),
                    status: item.data().Status,
                    complemento: item.data().Complemento 
                })
            })
            
            const lastDoc = col.docs[col.docs.length -1]

            setChamados(chamados => [...chamados, ...minhaLista])
            setLastDocs(lastDoc)

        }else{
            setIsEmpty(true)
        }

        setLoadingMore(false)
        setLoading(false)
        })


    }


    if(loading){
        return(
            <div>
                <Header/>
                <div className="content">
                    <Title name='Atendimentos' >
                        <FiMessageSquare size={25}/>
                    </Title>

                    <div className="container dashboard">
                        <span>Buscando chamados...</span>
                    </div>
                </div>    
            </div>
        )
    }

    return(
        <div>
            <Header/>
            <div className="content">
                <Title name='Atendimentos' >
                    <FiMessageSquare size={25}/>
                </Title>

                {chamados.length == 0 ? (
                    <div className="container dashboard" >
                        <span>Nenhum chamado registrado...</span>
                        <Link to='/new' className='new'>
                        <FiPlus size={25} color='#fff'/>
                        Novo chamado
                        </Link>
                    </div>
                ) : (
                    <>
                        <Link to='/new' className='new'>
                        <FiPlus size={25} color='#fff'/>
                        Novo chamado
                        </Link>

                        <table>
                            <thead>
                                <tr>
                                    <th scope="col">Clientes</th>
                                    <th scope="col">Assuntos</th>
                                    <th scope="col">Status</th>
                                    <th scope="col">Cadastrado em</th>
                                    <th scope="col">#</th>
                                </tr>
                            </thead>

                            <tbody>
                                {chamados.map((item,index)=>{
                                    return(
                                    <tr key={index}>
                                        <td data-label='Cliente'>{item.cliente}</td>
                                        <td data-label='Assunto'>{item.assunto}</td>
                                        <td data-label='Status'>
                                            <span className="badge" style={{backgroundColor: item.status === 'Aberto' ? '#5cb85c' : '#999'}}>{item.status}</span>
                                        </td>
                                        <td data-label='Cadastrado'>{item.createdFormated}</td>
                                        <td data-label='#'>
                                            <button style={{backgroundColor: '#3583f6'}} className='action' onClick={ () => togglePostModal(item)} >
                                            <FiSearch color="#fff" size={20}/>
                                            </button>
                                            <Link style={{backgroundColor: '#f6a935'}} className='action' to={`/new/${item.id}`} >
                                            <FiEdit2 color="#fff" size={20}/>
                                            </Link>
                                        </td>
                                    </tr>
                                    )
                                })}
                                
                            </tbody>
                        </table>
                        {loadingMore && <h3 style={{textAlign: 'center', marginTop: 15}} >Buscando dados...</h3>}
                        { !loadingMore && !isEmpty && <button className="btn-more" onClick={handleMore}>Buscar Mais</button>}

                    </>
                )}
                
            </div>

            {showPostModal && (
                <Modal  
                    conteudo={detail}
                    close={togglePostModal}
                />
            )}

        </div>
    )
}