import { useState, useContext } from 'react';
import { Link } from 'react-router-dom';
import {AuthContext} from '../../contexts/user';
import { toast } from "react-toastify";
import './signin.css'
import logo from '../../assets/logo.png'
function SignIn() {

  const [email,setEmail] = useState('')
  const [password,setPassword] = useState('')
  const { signIn, loadingAuth } = useContext(AuthContext)

  function handleSubmit(event){
    event.preventDefault()
    if(email != '' && password != ''){
      signIn(email, password)
    }else{
      toast.error('Algo deu Errado! Verifique suas Credenciais.')
    }
    
  }




    return (
      <div className='container-center'>
        <div className='login' >
          <div className='logo-area'>
            <img src={logo} alt='Logo do Sistema'/>
          </div>
          <form onSubmit={handleSubmit} >
            <h1>Entrar</h1>
            <input type='email' placeholder='Digite seu e-mail' value={email} onChange={ (event) => setEmail(event.target.value) } />
            <input type='password' placeholder='Digite sua senha' value={password} onChange={ (event) => setPassword(event.target.value)}/>
            <button type='submit'>{loadingAuth ? 'Carregando...' : 'Entrar'}</button>
          </form>

          <Link to='/register' >Criar uma conta</Link>

        </div>
        
      </div>
    );
  }
  
  export default SignIn;
  