import Header from '../../components/Header'
import { useState,useEffect, useContext } from 'react'
import Title from '../../components/Title'
import './new.css'
import {FiPlusCircle} from 'react-icons/fi'
import { AuthContext } from '../../contexts/user'
import { db } from '../../services/firebase'
import { useParams, useHistory } from 'react-router-dom'
import { doc,getDoc,addDoc,updateDoc, collection, onSnapshot } from 'firebase/firestore'
import { toast } from 'react-toastify'

export default function New(){

    const {id} = useParams()
    const history = useHistory()
    const [customers, setCustomers] = useState([])
    const [loadCustomers,setLoadCustomers] = useState(true)
    const [customerSelected,setCustomerSelected] = useState(0)
    const [assunto,setAssunto] = useState('Suporte')
    const [status,setStatus] = useState('Aberto')
    const [complemento, setComplemento] = useState('')
    const [idCustomer, setIdCustomer] = useState(false)
    const {user} = useContext(AuthContext)


    useEffect(()=>{

        async function loadCustomers(){

            const colRef = collection(db,'clientes')
            onSnapshot(colRef, (snapshot)=>{
                let lista = []
                snapshot.docs.forEach((item)=>{
                    lista.push({
                        id: item.id,
                        NomeFantasia: item.data().NomeFantasia
                    })
                })

                if(lista.length == 0){
                    console.log('nenhuma empresa encontrada')
                    setCustomers([{id: '1', NomeFantasia: 'freela'}])
                    setLoadCustomers(false)
                    return
                }
                setCustomers(lista)
                setLoadCustomers(false);

                if(id){
                    loadId(lista)
                }
                


            })
        }

        loadCustomers()


    },[])


    async function loadId(lista){
        const docRef = doc(db, 'chamados', id)
        const docSnap = await getDoc(docRef)
        if(docSnap.exists()){
            setAssunto(docSnap.data().Assunto)
            setStatus(docSnap.data().Status)
            setComplemento(docSnap.data().Complemento)
            let index = lista.findIndex(item => item.id == docSnap.data().ClienteID)
            setCustomerSelected(index)
            setIdCustomer(true)
            
        }
    }

    async function handleRegister(e){
        e.preventDefault()

        if(idCustomer){
            const docRef = doc(db, "chamados", id)
            await updateDoc(docRef, {
                Cliente: customers[customerSelected].NomeFantasia,
                ClienteID: customers[customerSelected].id,
                Assunto: assunto,
                Status: status,
                Complemento: complemento,
                UserID: user.uid
              }).then(()=>{
                  console.log('aqui dentro')
                  toast.success('Chamado editado com sucesso!')
                  setCustomerSelected(0)
                  setComplemento('')
                  history.push('/dashboard')
                  
              }).catch((error)=>{
                  toast.error('Erro ao editar. Tente novamente mais tarde')
                  console.log(error)
              })
              return;
        }

        
        const docRef = await addDoc(collection(db, 'chamados'),{
            Created: new Date(),
            Cliente: customers[customerSelected].NomeFantasia,
            ClienteID: customers[customerSelected].id,
            Assunto: assunto,
            Status: status,
            Complemento: complemento,
            UserID: user.uid
        }).then(()=>{
            toast.success('Chamado criado com sucesso!')
            setComplemento('')
            setCustomerSelected(0)
        })
        .catch((error)=>{
            toast.error('Algo deu errado! Tente novamente mais tarde')
            console.log(error)
        })
    }

    function handleChangeSelect(e){
        setAssunto(e.target.value)
    }

    function handleOptionChange(e){
        setStatus(e.target.value)
    }

    function handleChangeCustomers(e){
        setCustomerSelected(e.target.value)
    }

    return(
        <div>
            <Header/>

            <div className='content' >

            <Title name='Novo chamado' >
                <FiPlusCircle size={25}/>
            </Title>

            <div className='container'>

            <form className='form-profile' onSubmit={handleRegister} >

                <label>Cliente</label>

                {loadCustomers ? (

                    <input type='text' disabled={true} value='Carregando clientes...'/>
                ) : (

                    <select value={customerSelected} onChange={handleChangeCustomers} >
                    {customers.map((item,index)=>{
                        return(
                            <option key={item.id} value={index}>
                                {item.NomeFantasia}
                            </option>
                        )
                    })}
                    </select>

                )}

               

                <label>Assunto</label>

                <select value={assunto} onChange={handleChangeSelect}>
                    <option value='Suporte' >Suporte</option>
                    <option value='VisitaTec'>Visita Tecnica</option>
                    <option value='Financeiro'>Financeiro</option>
                </select>

                <label>Status</label>

                <div className='status'>

                    <input type='radio' name='radio' value='Aberto' onChange={handleOptionChange} checked={ status == 'Aberto'} />
                    <span>Em Aberto</span>

                    <input type='radio' name='radio' value='Progresso' onChange={handleOptionChange} checked={ status == 'Progresso'} />
                    <span>Em Progresso</span>

                    <input type='radio' name='radio' value='Atendido' onChange={handleOptionChange} checked={ status == 'Atendido'} />
                    <span>Finalizado</span>

                </div>

                <label>Complemento</label>

                <textarea type='text' placeholder='Descreva seu problema (opcional)' value={complemento} onChange={(event)=> setComplemento(event.target.value)} />

                <button type='submit' >Registrar</button>

            </form>

            </div>

            </div>
            
        </div>
    )
}