import { useState,useContext } from 'react';
import { Link } from 'react-router-dom';
import logo from '../../assets/logo.png'
import {AuthContext} from '../../contexts/user'
function SignUp() {

  const [nome, setNome] = useState('')
  const [sobrenome, setSobrenome] = useState('')
  const [email,setEmail] = useState('')
  const [password,setPassword] = useState('')
  const {signUp, loadingAuth} = useContext(AuthContext)


  function handleSubmit(event){
    event.preventDefault()
    if(nome != '' && password != '' && email != ''){
      signUp(email,password,nome,sobrenome)
    }
  }




    return (
      <div className='container-center'>
        <div className='login' >
          <div className='logo-area'>
            <img src={logo} alt='Logo do Sistema'/>
          </div>
          <form onSubmit={handleSubmit} >
            <h1>Crie uma Conta</h1>
            <input type='text' placeholder='Digite o seu nome' value={nome} onChange={(event)=> setNome(event.target.value)} />
            <input type='text' placeholder='Digite o seu sobrenome' value={sobrenome} onChange={(event)=> setSobrenome(event.target.value)} />
            <input type='email' placeholder='Digite seu e-mail' value={email} onChange={ (event) => setEmail(event.target.value) } />
            <input type='password' placeholder='Digite sua senha' value={password} onChange={ (event) => setPassword(event.target.value)}/>
            <button type='submit'>{loadingAuth ? 'Carregando...' : 'Realizar Cadastro'}</button>
          </form>

          <Link to='/'>Ja possui uma conta?</Link>

        </div>
        
      </div>
    );
  }
  
  export default SignUp;
  